<template>

    <div>
        <!--  -->
        <div v-if="state.loading">
            <p align="center" style="font-size:10px;margin-top: 5px;color: darkgray">...</p>
        </div>

        <!--   -->
        <div v-else-if="!items.length">
            <p align="center" style="font-size:10px;margin-top: 5px;color: darkgray"> </p>
        </div>

        <!--   -->
        <div v-else v-for="item in sanitizedItems">
            <event :item="item"/>
        </div>
    </div>

</template>

<script>

    import event from '../voteFeed/event/Event.vue';
    import { mapState } from 'vuex';
    export default {
        name: "userFeed",
        props: ['userId'],
        computed: {
            ...mapState('userFeed', {
                state: s => s,
                items: s => s.items,
            }),

            sanitizedItems(){
                let {items} = this;

                return items.map(item=>{
                    let {eventId, feedEventType} = item;
                    if (feedEventType) item.type = feedEventType;
                    // Do the ID swapping
                    if (item.type === "POLL_CREATED"){
                        item.pollId = eventId;
                        item.isPollEvent = true;
                    } else {
                        item.voteId = eventId;
                    }


                    return item;
                });
            }
        },


        created(){
            let {userId} = this;
            if (!userId){
                console.warn('User not passed to props of User Feed');
                return;
            }
            //console.log(user);
            console.log('hi')
            this.$store.dispatch(`userFeed/list`, {customUrl: `http://192.168.1.39:7070/rest/getFeedByUserId/${userId}`});
        },

        components: {
            event,
        },

        watch: {
            userId: function(userId){
                if (!userId){
                    console.warn('User not passed to props of User Feed');
                    return;
                }
                //console.log(user);
                this.$store.dispatch(`userFeed/list`, {customUrl: `http://192.168.1.39:7070/rest/getFeedByUserId/${userId}`});
            }
        }
    }
</script>

